import { query_tushare } from './utils.js';

const fields = [
  'l1_code',
  'l1_name',
  'l2_code',
  'l2_name',
  'l3_code',
  'l3_name',
  'ts_code',
  'name',
  'in_date',
  'out_date',
  'is_new',
];

export default async (ts_code) => {
  let res = await query_tushare('index_member_all', fields, {
    ts_code,
  });
  res = res[0];
  return res;
};
