import React, { useEffect, useState } from 'react';

import query from './query';

const search = new URLSearchParams(window.location.search);
let ts_code = search.get('code');

if (!ts_code) {
  ts_code = localStorage.getItem('code') || '300274.SZ';
  localStorage.setItem('code', ts_code);
}

function App() {
  const [data, setData] = useState(null);

  useEffect(() => {
    query(ts_code).then((data) => {
      setData(data);
    });
  }, []);

  if (!data) {
    return null;
  }
  console.log(data);

  return <div>3</div>;
}

export default App;
