import { query_tushare } from './utils.js';

const exchangeMap = {
  SSE: '上交所',
  SZSE: '深交所',
  BSE: '北交所',
};

const is_hsMap = {
  N: '否',
  H: '沪股通',
  S: '深股通',
};

const list_statusMap = {
  L: '上市',
  D: '退市',
  P: '暂停上市',
};

const fields = [
  'ts_code',
  'symbol',
  'name',
  'area',
  'industry',
  'cnspell',
  'market',
  'list_date',
  'act_name',
  'act_ent_type',
  'fullname',
  'enname',
  'exchange',
  'curr_type',
  'list_status',
  'delist_date',
  'is_hs',
];

export default async (ts_code) => {
  let res = await query_tushare('stock_basic', fields, {
    ts_code,
  });
  res = res[0];
  res.exchange = exchangeMap[res.exchange];
  res.is_hs = is_hsMap[res.is_hs];
  res.list_status = list_statusMap[res.list_status];
  return res;
};
