import dayjs from 'dayjs';
import { round, subtract, divide, multiply } from 'mathjs';

const trans = (data) => {
  const yi = [];
  const wan = ['circ_mv', 'total_mv'];

  const { fields, items } = data;
  return items.map((item) => {
    const obj = {};
    item.forEach((value, index) => {
      const key = fields[index];
      if (value !== null) {
        if (yi.includes(key)) {
          // 元 -> 亿
          obj[key] = round(value / 100000000, 1);
        } else if (wan.includes(key)) {
          // 万 -> 亿
          obj[key] = round(value / 10000, 1);
        } else if (typeof value === 'number') {
          obj[key] = round(value, 1);
        } else {
          obj[key] = value;
        }
      }
    });
    return obj;
  });
};
export const query_tushare = async (api_name, fields, params) => {
  return fetch(`${window.location.protocol}//api.tushare.pro`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      api_name,
      fields,
      params,
      token: 'fe77ac557743da478707c151abbe7b8f2dbdee278c983b2c82907827',
    }),
  })
    .then((response) => response.json())
    .then((response) => {
      const data = response.data;
      if (!data) {
        throw new Error(response?.msg);
      }
      const items = data.items;
      if (Array.isArray(items)) {
        return trans(data);
      } else {
        throw new Error(`items type is ${typeof items}`);
      }
    })
    .catch((error) => {
      const hint = `catch fetch(${api_name}，${params.ts_code}) error：${error.message}`;
      console.error(hint);
      return null;
    });
};

// 获取上市时间的第二年第一天，如果超过20年，则取20年前的第一天
export const get_start_date_from_list_date = (list_date) => {
  const begin_date = `${dayjs().year() - 20}0101`;
  if (list_date < begin_date) {
    return begin_date;
  } else {
    return `${dayjs(list_date).year() + 1}0101`;
  }
};

// 合并两个数据，相同的 end_date 取 update_flag 为 1 的数据，没有 1 的取 0 的数据
export const merge_update_flag = (res1, res2) => {
  if (res1.length || res2.length) {
    let data = [].concat(res1, res2);

    // 处理 update_flag
    data = data.filter((v) => {
      // 如果 update_flag 为 1，直接返回
      if (v.update_flag === '1') {
        return true;
      }
      // 如果 update_flag 为 0，判断是否有 update_flag 为 1 的数据，有则丢弃当前数据
      const has = data.findIndex((vv) => {
        return vv.end_date === v.end_date && vv.update_flag === '1';
      });
      if (has === -1) {
        return true;
      }
    });
    data = data.sort((a, b) => a.end_date - b.end_date);
    return data;
  }
  return [];
};

// 计算变动幅度
export const calc_zd = (start, end, n = 2) => {
  if (start && end) {
    return round(multiply(divide(subtract(end, start), start), 100), n);
  }
  return undefined;
};

// 计算去年同期的同比
export const calc_xxx_yoy = (data, key) => {
  data.forEach((item) => {
    const preYear = dayjs(item.end_date).subtract(1, 'year');
    const prev = data.find((v) => {
      return dayjs(v.end_date).isSame(preYear, 'month');
    })?.[key];
    item[`${key}_yoy`] = calc_zd(prev, item[key], 1);
  });
  return data;
};

// 计算比重
export const calc_bi_zhong_hint = (fen_zi, fen_mu) => {
  if (fen_zi > 0 && fen_mu > 0) {
    return round(multiply(divide(fen_zi, fen_mu), 100), 1) + '%';
  }
  return undefined;
};

// 过滤出从上市时间的第二年开始的年报数据
export const filter_after_list_date_12_month = (data, start_date) => {
  // 过滤出从上市时间的第二年开始的年报
  const month_12 = data.filter((item) => {
    const is1 = dayjs(item.end_date).isSameOrAfter(start_date, 'day');
    const is2 = dayjs(item.end_date).month() === 11;
    return is1 && is2;
  });
  // 如果最后一个月不是12月，则加上最新的一条数据
  const last = data.at(-1);
  if (dayjs(last.end_date).month() !== 11) {
    month_12.push(last);
  }
  return month_12;
};
