import { query_tushare } from './utils.js';

const fields = [
  'ts_code',
  'com_name',
  'com_id',
  'chairman',
  'manager',
  'secretary',
  'reg_capital',
  'setup_date',
  'province',
  'city',
  'introduction',
  'website',
  'email',
  'office',
  'business_scope',
  'employees',
  'main_business',
  'exchange',
  'ann_date',
];

export default async (ts_code) => {
  let res = await query_tushare('stock_company', fields, {
    ts_code,
  });
  res = res[0];
  res.website = 'https://' + res.website;
  return res;
};
