import { get_start_date_from_list_date } from './utils';

import query_stock_basic from './stock_basic';
import query_fina_indicator from './fina_indicator';
import query_stock_company from './stock_company';
import query_index_member_all from './index_member_all';
import query_income from './income';
import query_cashflow from './cashflow';
import query_stk_rewards from './stk_rewards';

export default async (ts_code) => {
  const stock_basic = await query_stock_basic(ts_code);

  const start_date = get_start_date_from_list_date(stock_basic.list_date);

  const [
    stock_company,
    index_member_all,
    fina_indicator,
    income,
    cashflow,
    stk_rewards,
  ] = await Promise.all([
    query_stock_company(ts_code), // 公司信息
    query_index_member_all(ts_code), // 指数成分
    query_fina_indicator(ts_code, start_date), // 财务指标
    query_income(ts_code, start_date), // 利润表
    query_cashflow(ts_code, start_date), // 现金流量表
    query_stk_rewards(ts_code), // 管理层薪酬和持股
  ]);

  return {
    stock_basic,
    stock_company,
    index_member_all,
    fina_indicator,
    income,
    cashflow,
    stk_rewards,
  };
};
