import { query_tushare } from './utils.js';

const fields = [
  'ts_code',
  'ann_date',
  'end_date',
  'name',
  'title',
  'reward',
  'hold_vol',
];
const api_name = 'stk_rewards';

export default async (ts_code) => {
  const res = await query_tushare(api_name, fields, {
    ts_code,
  });
  return res;
};
